import { useQuery } from "@tanstack/react-query"
import { getAllPrivateFilesOwners } from "../services/getAllPrivateFilesOwners"

function useFilesOwnersList() {
  return useQuery({
    queryKey: ["get-all-private-files-owners"],
    queryFn: getAllPrivateFilesOwners,
  })
}

export { useFilesOwnersList }
